.van-loading,
.van-loading__spinner {
  position: relative;
  vertical-align: middle;
}

.van-loading {
  font-size: 0;
}

.van-loading__spinner {
  display: inline-block;
  width: 0.3rem;
  max-width: 100%;
  height: 0.3rem;
  max-height: 100%;
  -webkit-animation: van-rotate 0.8s linear infinite;
  animation: van-rotate 0.8s linear infinite;
}

.van-loading__spinner--spinner {
  -webkit-animation-timing-function: steps(12);
  animation-timing-function: steps(12);
}

.van-loading__spinner--spinner i {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.van-loading__spinner--spinner i::before {
  display: block;
  width: 0.02rem;
  height: 25%;
  margin: 0 auto;
  background-color: currentColor;
  border-radius: 40%;
  content: ' ';
}

.van-loading__spinner--circular {
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
}

.van-loading__circular {
  display: block;
  width: 100%;
  height: 100%;
}

.van-loading__circular circle {
  -webkit-animation: van-circular 1.5s ease-in-out infinite;
  animation: van-circular 1.5s ease-in-out infinite;
  stroke: currentColor;
  stroke-width: 3;
  stroke-linecap: round;
}

.van-loading__text {
  display: inline-block;
  margin-left: 0.08rem;
  color: #969799;
  font-size: 0.14rem;
  vertical-align: middle;
}

.van-loading--vertical {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.van-loading--vertical .van-loading__text {
  margin: 0.08rem 0 0;
}

.van-loading__spinner--spinner i:nth-of-type(1) {
  -webkit-transform: rotate(30deg);
  transform: rotate(30deg);
  opacity: 1;
}

.van-loading__spinner--spinner i:nth-of-type(2) {
  -webkit-transform: rotate(60deg);
  transform: rotate(60deg);
  opacity: 0.9375;
}

.van-loading__spinner--spinner i:nth-of-type(3) {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  opacity: 0.875;
}

.van-loading__spinner--spinner i:nth-of-type(4) {
  -webkit-transform: rotate(120deg);
  transform: rotate(120deg);
  opacity: 0.8125;
}

.van-loading__spinner--spinner i:nth-of-type(5) {
  -webkit-transform: rotate(150deg);
  transform: rotate(150deg);
  opacity: 0.75;
}

.van-loading__spinner--spinner i:nth-of-type(6) {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
  opacity: 0.6875;
}

.van-loading__spinner--spinner i:nth-of-type(7) {
  -webkit-transform: rotate(210deg);
  transform: rotate(210deg);
  opacity: 0.625;
}

.van-loading__spinner--spinner i:nth-of-type(8) {
  -webkit-transform: rotate(240deg);
  transform: rotate(240deg);
  opacity: 0.5625;
}

.van-loading__spinner--spinner i:nth-of-type(9) {
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
  opacity: 0.5;
}

.van-loading__spinner--spinner i:nth-of-type(10) {
  -webkit-transform: rotate(300deg);
  transform: rotate(300deg);
  opacity: 0.4375;
}

.van-loading__spinner--spinner i:nth-of-type(11) {
  -webkit-transform: rotate(330deg);
  transform: rotate(330deg);
  opacity: 0.375;
}

.van-loading__spinner--spinner i:nth-of-type(12) {
  -webkit-transform: rotate(360deg);
  transform: rotate(360deg);
  opacity: 0.3125;
}

.van-button {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  height: 0.44rem;
  margin: 0;
  padding: 0;
  font-size: 0.16rem;
  line-height: 0.42rem;
  text-align: center;
  border-radius: 0.02rem;
  -webkit-appearance: none;
  -webkit-text-size-adjust: 100%;
}

.van-button::before {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  background-color: #000;
  border: inherit;
  border-color: #000;
  border-radius: inherit;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  opacity: 0;
  content: ' ';
}

.van-button:active::before {
  opacity: 0.1;
}

.van-button--disabled::before,
.van-button--loading::before {
  display: none;
}

.van-button--default {
  color: #323233;
  background-color: #fff;
  border: 0.01rem solid #ebedf0;
}

.van-button--primary {
  color: #fff;
  background-color: #07c160;
  border: 0.01rem solid #07c160;
}

.van-button--info {
  color: #fff;
  background-color: #1989fa;
  border: 0.01rem solid #1989fa;
}

.van-button--danger {
  color: #fff;
  background-color: #f44;
  border: 0.01rem solid #f44;
}

.van-button--warning {
  color: #fff;
  background-color: #ff976a;
  border: 0.01rem solid #ff976a;
}

.van-button--plain {
  background-color: #fff;
}

.van-button--plain.van-button--primary {
  color: #07c160;
}

.van-button--plain.van-button--info {
  color: #1989fa;
}

.van-button--plain.van-button--danger {
  color: #f44;
}

.van-button--plain.van-button--warning {
  color: #ff976a;
}

.van-button--large {
  width: 100%;
  height: 0.5rem;
  line-height: 0.48rem;
}

.van-button--normal {
  padding: 0 0.15rem;
  font-size: 0.14rem;
}

.van-button--small {
  min-width: 0.6rem;
  height: 0.3rem;
  padding: 0 0.08rem;
  font-size: 0.12rem;
  line-height: 0.28rem;
}

.van-button__loading {
  display: inline-block;
  vertical-align: top;
}

.van-button--mini {
  display: inline-block;
  min-width: 0.5rem;
  height: 0.22rem;
  font-size: 0.1rem;
  line-height: 0.2rem;
}

.van-button--mini + .van-button--mini {
  margin-left: 0.05rem;
}

.van-button--block {
  display: block;
  width: 100%;
}

.van-button--disabled {
  opacity: 0.5;
}

.van-button--hairline.van-button--round::after,
.van-button--round {
  border-radius: 10em;
}

.van-button--hairline.van-button--square::after,
.van-button--square {
  border-radius: 0;
}

.van-button__icon {
  min-width: 1em;
  font-size: 1.2em;
  line-height: inherit;
  vertical-align: top;
}

.van-button__icon + .van-button__text,
.van-button__loading + .van-button__text {
  display: inline-block;
  margin-left: 0.05rem;
  vertical-align: top;
}

.van-button--hairline {
  border-width: 0;
}

.van-button--hairline::after {
  border-color: inherit;
  border-radius: 0.04rem;
}

.van-cell {
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  box-sizing: border-box;
  width: 100%;
  padding: 0.1rem 0.16rem;
  overflow: hidden;
  color: #323233;
  font-size: 0.14rem;
  line-height: 0.24rem;
  background-color: #fff;
}

.van-cell:not(:last-child)::after {
  position: absolute;
  box-sizing: border-box;
  content: ' ';
  pointer-events: none;
  right: 0;
  bottom: 0;
